    /* Colors for the work page */
    --work-bg: #fbf3f4;
    --work-purple: #8568b1;
    --work-light-purple: #9b96b7;
    --work-pink: #edc4d9;
    --work-medium-purple: #c6a2d9;
    --work-light-blue: #d0d8ee;
    --work-peach: #dfc5b0;
    --work-text-gray: #807e7c;

    /* Colors for the about page */
    --about-bg: #f2faf9;
    --about-dark-blue: #435963;
    --about-green: #9bcdba;
    --about-light-green: #c3d2cf;
    --about-gray-green: #b5bebb;
    --about-text-gray: #7c8484;
    --about-blue-gray: #7c7c9f;
    --about-light-blue: #b4b3c5;
    --about-very-light-blue: #d5d3d9;

    /* System colors */
    --background: 180 36% 97%;
    --foreground: 240 26% 30%;
    --card: 0 0% 100%;
    --card-foreground: 240 10% 3.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 240 10% 3.9%;
    --primary: 256 75% 52%;
    --primary-foreground: 0 0% 98%;
    --secondary: 240 4.8% 95.9%;
    --secondary-foreground: 240 5.9% 10%;
    --muted: 240 4.8% 95.9%;
    --muted-foreground: 240 3.8% 46.1%;
    --accent: 180 47% 76%;
    --accent-foreground: 243 51% 30%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 0 0% 98%;
    --border: 240 5.9% 90%;
    --input: 240 5.9% 90%;
    --ring: 240 5.9% 10%;
    --radius: 0.5rem;
  }

*{
  border-color: hsl(var(--border));
}

body{
  background-color: hsl(var(--background));
  color: hsl(var(--foreground));
    font-family: 'Bitter', serif;
}

.avatar-wrap {
    /* Style de base */
    position: relative;
    perspective: 1000px;
  }

.avatar {
    /* Style de l'avatar */
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
  }

/* Style pour le motif hachuré */

.stripes {
    background-image: linear-gradient(
      45deg,
      #e0f2fe 25%,
      transparent 25%,
      transparent 75%,
      #e0f2fe 75%
    );
    background-size: 20px 20px;
    opacity: 0.5;
  }

/* Styles pour les éléments SVG */

.face-outline path {
    fill: #ffffff;
    stroke: #7C3AED;
    stroke-width: 3;
  }

.glasses rect, .glasses line {
    fill: none;
    stroke: #7C3AED;
    stroke-width: 3;
  }

.eyes circle {
    fill: #7C3AED;
  }

.mouth path {
    stroke: #7C3AED;
    stroke-width: 3;
  }

.hair path {
    stroke: #7C3AED;
    stroke-width: 3;
    fill: none;
  }

/* Animation d'entrée */

.avatar--ready {
    animation: fadeIn 0.5s ease-out;
  }

@keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

h1, h2, h3, h4, h5, h6 {
    font-family: 'Bitter', serif;
    font-weight: 700;
  }

.dot{
  color: var(--purple);
}

.stripes {
    background-image: repeating-linear-gradient(
      135deg,
      var(--light-blue),
      var(--light-blue) 5px,
      var(--off-white) 5px,
      var(--off-white) 10px
    );
  }

.dots {
    background-image: radial-gradient(
      var(--light-blue) 1px,
      transparent 1px
    );
    background-size: 8px 8px;
  }

